.banner {
    background-image: url(../../assets/images/banner-bg.svg);
    background-position: right bottom;
    background-repeat: no-repeat;
    background-size: 58%;
    height: 850px;
    padding: 65px 0 0 0;
    overflow: hidden;
}
.banner h1 {
    margin-bottom: 70px;
}
.banner-text {
    color: #000;
    font-size: 25px;
    font-style: normal;
    font-weight: 500;
    line-height: 35px;
    letter-spacing: -0.015em;
    text-align: left;
    margin-bottom: 27px;
}
.banner-text span {
    font-size: 30px;
    color: #0629CD;
    text-transform: uppercase;
    font-weight: bold;
}
.banner-text strong {
    color: #0629CD;
    font-size: 50px;
    font-style: normal;
    font-weight: 700;
    line-height: 35px;
    letter-spacing: -0.015em;
    text-align: left;
}
.pricing-btn,
.pricing-btn:hover,
.pricing-btn:focus {
    background-color: #FFF300;
    color: #121212;
    font-size: 17px;
    font-style: normal;
    padding-top: 11px;
    font-weight: 600;
    line-height: 21px;
    letter-spacing: -0.015em;
    text-align: center;
    height: 43px;
    width: 176px;
    border-radius: 6px;
    display: inline-block;
    margin-right: 16px;
}








