body {
  -webkit-transition: all .3s ease-in-out;
	-moz-transition: all .3s ease-in-out;
	-o-transition: all .3s ease-in-out;
	transition: all .3s ease-in-out;
  font-family: 'Montserrat', sans-serif;
	margin:0;
	font-size:14px;
}
.header-container {
  max-width: 1500px;
  margin: 0 auto;
  width: 100%;
}
.container {
    max-width: 1400px;
    margin: 0 auto;
    width: 100%;
}
ul {
	margin:0 0 0 0;
	padding:0 0 0 0;
}
ul li {
  list-style: none;
}
a,
a:hover,
a:focus {
	outline:none;
	text-decoration:none;
}

h1 {
    font-family: Montserrat;
    color: #000;
    font-size: 56px;
    font-style: italic;
    font-weight: 700;
    line-height: 80px;
    letter-spacing: -0.015em;
}
h1 span {
  color: #ff0000;
}
h2 {
    color: #000;
    font-size: 36px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: -0.015em;
}


