.demo {
    padding: 75px 0 50px;
    text-align: left;
}
.demo h2 {
    line-height: 55px;
}
.demo-btn,
.demo-btn:hover,
.demo-btn:focus {
    background-color: #FFF300;
    color: #121212;
    font-size: 17px;
    font-style: normal;
    padding-top: 11px;
    font-weight: 600;
    line-height: 20px;
    letter-spacing: -0.015em;
    margin-top: 20px;
    text-align: center;
    height: 43px;
    width: 240px;
    border-radius: 6px;
    display: inline-block;
}
