.sercices-section {
    padding: 64px 0 104px;
    box-shadow: 0px 4px 4px 0px #00000040;
    text-align: center;
}

.sercices-text {
    background-color: #000;
    padding: 45px;
    margin-top: -50px;
    text-align: left;
}
.sercices-text h3 {
    color: #fff;
    font-size: 36px;
    font-style: normal;
    font-weight: 600;
    line-height: 55px;
    letter-spacing: -0.015em;
}
.sercices-text p {
    color: #fff;
    font-size: 17px;
    font-style: normal;
    font-weight: 500;
    line-height: 25px;
    letter-spacing: -0.015em;
    margin-bottom: 40px;
}
.sercices-text button {
    background-color: #FFE147;
    color: #000;
    font-size: 15px;
    line-height: 18px;
    text-align: center;
    letter-spacing: -0.015em;
    font-weight: 600;
    border: none;
    border-radius: 4px;
    display: inline-block;
    width: 140px;
    height: 26px;
}
